import { ConvertToPluginCommands } from '@ali/4ever-cangjie';
import createClearStyle from './clearStyles';


export const createCommands = (clearStyleConfig: any) => {
  const ignores = [...(clearStyleConfig.ignores || []), 'unlink'];
  return {
    clearStyle: createClearStyle(ignores),
  }
}

declare module "@ali/4ever-cangjie" {
  interface PluginCommands extends ConvertToPluginCommands<ReturnType<typeof createCommands>> {}
}
